.face-mask-base {
  position: absolute;
  top: 0;
}
.face-mask-base .container {
  position: absolute;
}

.face-mask-base .bounding-box {
  position: absolute;
  border: 2px solid greenyellow;
  -webkit-transform: scale(3);
      -ms-transform: scale(3);
          transform: scale(3);
  border-color: rgba(0, 0, 0, 0);
  -webkit-transition: border-color 1s, -webkit-transform 1s;
  transition: border-color 1s, -webkit-transform 1s;
  -o-transition: transform 1s, border-color 1s;
  transition: transform 1s, border-color 1s;
  transition: transform 1s, border-color 1s, -webkit-transform 1s;
}

.face-mask-base .bounding-box.found {
  border-color: rgb(0, 199, 0);
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.face-mask-base .bounding-box.not-found {
  border-color: rgb(189, 0, 0);
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
}

.face-mask-base circle {
  display: none;
}

.face-mask-base .mask-path {
  display: none;
  -webkit-animation: dash 5s linear forwards;
          animation: dash 5s linear forwards;
  -webkit-animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
          animation-timing-function: cubic-bezier(0.455, 0.030, 0.515, 0.955);
}  

@-webkit-keyframes dash {
  50% {
    stroke-dashoffset: 0;
  }
}  

@keyframes dash {
  50% {
    stroke-dashoffset: 0;
  }
}



